.nav
{
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    grid-column-gap: 14rem;
    -webkit-column-gap: 14rem;
            column-gap: 14rem;
    margin: auto;
    overflow: hidden;
}

    .entry-toggle-container
    {
        display: -webkit-flex;
        display: flex;
        -webkit-flex-direction: row;
                flex-direction: row;
        grid-column-gap: 0.4rem;
        -webkit-column-gap: 0.4rem;
                column-gap: 0.4rem; /* Media */
        height: 5rem;
    }
        .switch-label
        {
            margin: auto;
        }
        .entry-toggle-container > span
        {
            font-size: 2.2em;
            transition: color ease-in 0.2s;
            margin-top: 10px;
            font-family: "Staatliches";
            text-align: center;
            padding-top: 6px;
        }
    
    .active
    {
        color: white;
        text-shadow: 0 0 5px white;
        -webkit-animation: glow 3s ease-in;
                animation: glow 3s ease-in;

    }

    .inactive
    {
        color: black;
        text-shadow: 0 0 2px white;

    }

    @-webkit-keyframes shake {
        0% { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
       25% { -webkit-transform: rotate(45deg); transform: rotate(45deg); }
       50% { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
       75% { -webkit-transform: rotate(-45deg); transform: rotate(-45deg); }
      100% { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
    }

    @keyframes shake {
        0% { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
       25% { -webkit-transform: rotate(45deg); transform: rotate(45deg); }
       50% { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
       75% { -webkit-transform: rotate(-45deg); transform: rotate(-45deg); }
      100% { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
    }

    @-webkit-keyframes glow {
        from {
            text-shadow: 0 0 10px black;
        }
        to {
            text-shadow: 0 0 10px white; 
        }
    }


    /* mobile small */

    @media only screen and (max-width: 320px) {
